<template>
    <v-list v-if="room.state[name]" >
<!-- 
        <v-subheader>
            {{label}}
            <v-spacer></v-spacer>
        </v-subheader> -->


        <v-list-item>
            <v-list-item-action>
                <v-icon :color="isOn(room.state[name].power) ? '' : 'secondary'">{{ isOn(room.state[name].power) ? 'mdi-power-plug-outline' : 'mdi-power-plug-off-outline' }}</v-icon>
            </v-list-item-action>

            <v-list-item-content class="text-md-h2">
                <v-list-item-title>{{label}}</v-list-item-title>
            </v-list-item-content>

            <v-list-item-icon class="mt-0">
                <f-switch :label="room.state[name].power" :disabled="!isDeviceOnline || isOn(room.state[name].automation)" :value="room.state[name].power" :room="room" :control="`${name}.power`"/>
                <f-switch class="ml-2" :label="isOn(room.state[name].automation) ? 'Auto': 'Manual'" :disabled="!isDeviceOnline" :value="room.state[name].automation" :room="room" :control="`${name}.automation`"/>
            </v-list-item-icon>
        </v-list-item>

        <!-- <v-divider></v-divider> -->

        <!-- device -->
        <device-list-item :device="room.device[name]" :simple="true"></device-list-item>
        
        <!-- custom items -->
        <slot/>

        <template v-if="!expand">

            <v-divider></v-divider>

            <v-list-item :disabled="!isDeviceOnline"  @click="expand = true" dense>
              <v-list-item-action>
                <v-icon>mdi-chevron-down</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title class="text--secondary">Mais informações</v-list-item-title>              
              </v-list-item-content>
            </v-list-item>

        </template>

        <template v-if="expand">

            <!-- last on -->
            <v-divider></v-divider>
            <v-list-item :disabled="!isDeviceOnline">

                <v-list-item-icon>
                    <v-icon  :disabled="!isDeviceOnline">mdi-calendar-clock</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>Último ON</v-list-item-title>
                    <!-- <v-list-item-subtitle>OFF</v-list-item-subtitle> -->
                </v-list-item-content>

                <v-list-item-action>
                    <span v-if="room.state[name].last_on">{{ parseInt(room.state[name].last_on) | moment("DD/MM/YYYY, H:mm:ss")}}</span>
                    <span v-else>-</span>
                </v-list-item-action>

            </v-list-item>

            <!-- last on -->
            <v-divider></v-divider>
            <v-list-item :disabled="!isDeviceOnline">

                <v-list-item-icon>
                    <v-icon :disabled="!isDeviceOnline">mdi-calendar-clock</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>Último OFF</v-list-item-title>
                    <!-- <v-list-item-subtitle>OFF</v-list-item-subtitle> -->
                </v-list-item-content>

                <v-list-item-action>
                    <span v-if="room.state[name].last_off">{{ parseInt(room.state[name].last_off) | moment("DD/MM/YYYY, H:mm:ss")}}</span>
                    <span v-else>-</span>
                </v-list-item-action>

            </v-list-item>

        </template>

    </v-list>
    
</template>

<script>

import FSwitch from './Switch.vue'
import DeviceStatusListItem from './DeviceStatusListItem'
import DeviceListItem from '../device/DeviceListItem'

export default {
    props: ['room', 'label', 'name'],
    components: {
        FSwitch,
        DeviceStatusListItem,
        DeviceListItem
    },
    data: () => ({
        expand: false

    }),
    methods: {
        isOn: state => state == 'ON'        
    },
    computed: {
        isDeviceOnline() {
            let device = this.room.device[this.name]
            if (!device) return false
            return device.state == 'ready'
        }
    }
}
</script>