<template>

    <v-icon v-if="signal > 75"
        :dense="dense" 
        color="green lighten-1" :title="`${signal}%`">mdi-signal-cellular-3</v-icon>

    <v-icon v-else-if="signal >= 50"
        :dense="dense" 
        color="yellow lighten-2" :title="`${signal}%`">mdi-signal-cellular-2</v-icon>

    <v-icon v-else-if="signal > 0"
        :dense="dense" 
        :color="signal > 40 ? 'orange lighten-1' : 'red lighten-1'" :title="`${signal}%`">mdi-signal-cellular-1</v-icon>

    <v-icon v-else
        :dense="dense" 
        color="red darken-4">mdi-signal-off</v-icon>
    
</template>


<script>
export default {

    props: ['device', 'dense'],
    computed: {
        signal() {
            if (!this.device || this.device.state != 'ready' || !this.device.stats) return -1
            return parseInt(this.device.stats.signal)
        }
    }
    
    
}
</script>