import Vue from 'vue'
import { Duration } from 'moment'

interface FilterOptions {
  dense?: boolean;
  truncate?: boolean;
}

const filters = {
  duration(d: Duration, opts: FilterOptions = {}): string {
    const out = []
    if (!d) return ''

    const truncateH = opts.truncate && !d.hours()! && !d.minutes() && !d.seconds()
    const truncateM = opts.truncate && !d.minutes() && !d.seconds()
    const truncateS = opts.truncate && !d.seconds()

    // console.log('duration()', d.asSeconds(), d.asHours())
    
    if (Math.abs(d.asDays()) >= 1)
    {
      out.push(`${d.asDays()|0}d`)
      if (!truncateH) out.push(`${d.hours()}h`)
      if (!truncateM) out.push(`${d.minutes()}m`)
      // if (d.seconds()) out.push(`${d.seconds()}s`)
    }
    else if (Math.abs(d.asHours()) >= 1)
    {
      out.push(`${d.asHours()|0}h`)
      if (!truncateM) out.push(`${d.minutes()}m`)
      // if (d.seconds()) out.push(`${d.seconds()}s`)
    }
    else if (Math.abs(d.asMinutes()) >= 1)
    {
      out.push(`${d.asMinutes()|0}m`)
      if (!truncateS) out.push(`${d.seconds()}s`)
    }
    else {
      out.push(`${d.seconds()}s`)
    }

    return d.asSeconds() < 0
      ? '-' + out.join(opts.dense ? '' : ' ').replace(/-/g, '')
      : out.join(opts.dense ? '' : ' ')
  }
}

for (const [name, fn] of Object.entries(filters))
  Vue.filter(name, fn)
