






































import { Component, Vue, Prop, Inject } from 'vue-property-decorator';
import IdealRangeControl from '@/components/common/IdealRangeControl.vue'
import { Room } from '@/types'

import RelayControlList from "../common/RelayControlList.vue"


@Component({
  components: {
    IdealRangeControl,
    RelayControlList
  }
})
export default class HumidityControl extends Vue {

  @Prop() readonly room!: Room
  @Inject() publishControl!: Function

  onChange(param: string, value: number) {
    console.log("HumidityControl:onChange", param, value)
    this.publishControl(this.room, `humidity.${param}`, value)
  }

}


