













































import { Component, Vue, Inject } from 'vue-property-decorator';
import { Room } from '../types/'
import AlertList from "@/components/alert/AlertList.vue"
import RelayList from "@/components/relay/RelayList.vue"
import DeviceList from "@/components/device/DeviceList.vue"
import ProgressList from "@/components/common/ProgressList.vue"
import StateRow from '@/components/common/StateRow.vue'
import RoomSettings from '@/components/settings/RoomSettings.vue'

@Component({
  components: {
    AlertList,
    StateRow,
    RelayList,
    RoomSettings,
    ProgressList,
    DeviceList
  }
})
export default class Dashboard extends Vue {

  @Inject() toggleDrawer!: Function

  get rooms() {
    return this.$root.$data.rooms.filter( (r: Room )=> {
      return r.id != "waiting" && r.deviceCount > 0
    })
  }
  
}
