






















































import { Vue, Component, Prop } from 'vue-property-decorator';
import { Room } from "@/types"
import SettingListItem from "@/components/settings/SettingListItem.vue";


interface SettingMenuItem {
  id: string;
  title: string;
  icon: string;
  device: string;
}

@Component({
  components: {
    SettingListItem    
  }
})
export default class EbbflowSettings extends Vue
{
  @Prop() dense?: boolean
  @Prop() title?: string
  @Prop() readonly room!: Room

  showFineTune = false

  formatInterval = (v: number) => v ? `${v} minutos` : ''
  formatDate    = (v: number) => v ? this.$moment.unix(Number(v)).calendar() : ''

  mounted() {
    this.$emit('mounted')
  }



}
