



































import { Vue, Component, Prop } from 'vue-property-decorator';
import { Room } from "@/types"
import { RouterOptions } from 'vue-router';

@Component({
  components: {
  }
})
export default class EbbflowStateRow extends Vue
{
  @Prop() readonly dense?: boolean
  @Prop() readonly room!: Room
  @Prop() readonly to?: RouterOptions

  get currentPhase() {
    return Number(this.room.state.ebbflow?.phase)
  }

  get currentPhaseLabel()
  {
    switch (this.currentPhase) {
      case 1: return 'EMPTY';
      case 2: return 'DRAIN';
      case 3: return 'FLOOD';
      case 4: return 'FULL';
      default: return '';
    }
  }

  get currentPhaseAction()
  {
    switch (this.currentPhase) {
      case 1: return 'Vazio';
      case 2: return 'Drenando';
      case 3: return 'Enchendo';
      case 4: return 'Estabilizando';
      default: return '';
    }
  }

  get isFull() {
    return this.room.state.ebbflow?.is_full == "1"
  }

  get isEmpty() {
    return this.room.state.ebbflow?.is_empty == "1"
  }

  get fullColor() {
    return this.isFull ? '#FFF' : '#424242'
  }

  get emptyColor() {
    return this.isEmpty ? '#FFF' : '#424242'
  }
}
