<template>
    <v-switch 
        true-value="ON" 
        false-value="OFF"
        :input-value="value"
        :disabled="disabled"
        :hint="label"
        :persistent-hint="!!label"
        :color="color"
        @change="onChange"
        >
    </v-switch>
    
</template>

<script>
export default {
    props:['room', 'control', 'value', 'disabled', 'label', 'color'],
    inject: ['publishControl'],
    methods: {
        onChange(event) {
            if (this.room)
                this.publishControl(this.room, this.control, event)
            else
                this.$emit('change', event)
        }
    }

}
</script>