































































































import Vue from 'vue'
import { Device } from '../../types'

export default Vue.extend({
    name: 'About',
    data: () => (
    {
        show: false,
        device: null     ,
        options: {
            fullscreen: true
        }    
    }),
    methods: {
        open(device: Device) 
        {                
            this.$data.device = device
            this.$data.show = true
            console.log(device)
        },
        close() {
            this.$data.show = false
        }
    }
})
