<template>

    <v-dialog  v-model="dialog" 
        :max-width="options.width" 
        :style="{ zIndex: options.zIndex }" 
        :dark="options.dark"
        light
        persistent 
        width="290px" 
        @keydown.esc="cancel"
        @keydown.enter="ok">



        <v-card>
            <v-card-text class="pa-4 pb-0">
                <span v-if="options.text">{{ options.text }}</span>
                <v-text-field dense outlined autofocus :label="label" v-model="value"></v-text-field>
            </v-card-text>
            <v-card-actions>
                <v-btn text @click="cancel">Cancelar</v-btn>
                <v-btn text color="primary" @click="ok">OK</v-btn>
            </v-card-actions>
        </v-card>
        
    </v-dialog>
    
</template>


<script>
    export default {
        name: 'PromptDialog',
        data: () => (
        {
            dialog: false,
            resolve: null,
            reject: null,
            label: null,
            value: null,
            options: {
                text: null,
                color: null,
                zIndex: 200,
            }
        }),
        computed: {
            show: {
                get() {
                    return this.dialog
                },
                set(value) {
                    this.dialog = value
                    if (value === false) {
                        this.cancel()
                    }
                }
            }
        },
        methods: {
            open(label, value, options) 
            {
                this.label = label
                this.value = value
                this.options = Object.assign(this.options, options)
                this.dialog = true

                return new Promise((resolve, reject) => {
                    this.resolve = resolve
                    this.reject = reject
                })
            },
            ok() {
                this.resolve(this.value)
                this.dialog = false
            },
            cancel() {
                // this.reject()
                this.dialog = false
            }
        }
    }
</script>