
import { Component, Vue, Prop, Inject } from 'vue-property-decorator'
import { Room } from '../../types'
// import colors from 'vuetify/lib/util/colors'

@Component({
})
export default class EbbflowBase extends Vue  {

  @Prop() readonly room!: Room
  @Inject() publishControl!: Function

  minInterval = 30
  maxInterval = 900
  phases = ["INIT", "EMPTY", "DRAIN", "FLOOD", "FULL"]
  states = [{
      name: "Empty"
    },
    {
      name: "Drain"
    },
    {
      name: "Flood"
    },
    {
      name: "Full"
    }
  ]

  get state() { 
    return this.room.state.ebbflow 
  }

  get ebbflow() { 
    return this.room.device.ebbflow 
  }

  get deviceOnline() 
  {
    return this.ebbflow ? this.ebbflow.state == "ready" : false
  }

  get floodPump() { 
    return this.room.device.ebbflow_flood_relay 
  }

  get floodPumpOnline() { 
    return this.floodPump?.state == 'ready'
  }

  get drainPump() { 
    return this.room.device.ebbflow_drain_relay 
  }

  get drainPumpOnline() { 
    return this.drainPump?.state == 'ready'
  }

  get phaseLabel() {
    if (!this.state || !this.state.phase) return ''
    let label = this.phases[parseInt(this.state.phase)]
    return label ? label : ''
  }

  get currentPhaseDuration() 
  {
    let timestamp: string|undefined
    const state = this.room.state.ebbflow
    const phase = state?.phase
    
    if (!state || !phase) return ''    

    if      (phase == '1') timestamp = state.last_empty
    else if (phase == '2') timestamp = state.last_drain
    else if (phase == '3') timestamp = state.last_flood
    else if (phase == '4') timestamp = state.last_full

    if (!timestamp) return ''

    const seconds = this.$root.$data.unixTime - Number(timestamp)
    return this.$moment.duration(seconds, 'seconds')
  }

  get isDaytime() {
    return Number(this.room.state.daytime?.is_daytime) == 1
  }

  get currentPhase() {
    return Number(this.room.state.ebbflow?.phase)
  }
  
  get lastEmpty() {    
    return Number(this.room.state.ebbflow?.last_empty)
  }
  
  get lastDrain() {    
    return Number(this.room.state.ebbflow?.last_drain)
  }
  
  get lastFlood() {    
    return Number(this.room.state.ebbflow?.last_flood)
  }
  
  get lastFull() {    
    return Number(this.room.state.ebbflow?.last_full)
  }
  
  get maxFullSeconds() {
    return Number(this.room.state.ebbflow?.max_full_seconds)
  }

  get intervalDuration() {
    return this.isDaytime
      ? Number(this.room.state.ebbflow?.day_interval)*60
      : Number(this.room.state.ebbflow?.night_interval)*60
  }

  get ebbflowAutomationEnabled() {
    return this.state?.flood_automation == 'ON'
  }
  set ebbflowAutomationEnabled(enabled: boolean) {
    this.publishControl(this.room, 'ebbflow.flood_automation', enabled ? 'ON' : 'OFF')
  }

  get floodAutomationEnabled() {
    return this.room.state.ebbflow_flood_relay?.automation == 'ON'
  }
  set floodAutomationEnabled(enabled: boolean) {
    this.publishControl(this.room, 'ebbflow_flood_relay.automation', enabled ? 'ON' : 'OFF')
  }

  get drainAutomationEnabled() {
    return this.room.state.ebbflow_drain_relay?.automation == 'ON'
  }
  set drainAutomationEnabled(enabled: boolean) {
    this.publishControl(this.room, 'ebbflow_drain_relay.automation', enabled ? 'ON' : 'OFF')
  }

}