





















import { Component, Vue, Prop } from 'vue-property-decorator'
import { Device } from '../../types'

import DeviceListItem from "./DeviceListItem.vue"


@Component({
    components: {
        DeviceListItem
    }
})
export default class DeviceList extends Vue {

    @Prop() roomid?: string
    @Prop() dense?: boolean
    @Prop() title?: string
        
    get devices() {
        let devices: Device[] = Object.values(this.$root.$data.devices)
        // console.log("computing devices", this.roomid)
        if (this.roomid) {
            
            devices = devices.filter(d => d.roomid == this.roomid)
        }
        // console.log(devices)
        return devices
    }

}

