



















import { Vue, Component, Prop } from 'vue-property-decorator';
import { Room } from '@/types'
import ProgressListItem from '@/components/common/ProgressListItem.vue'
// import { RouterOptions } from 'vue-router';

@Component({
  components: {
    ProgressListItem
  }
})
export default class DayNightProgress extends Vue
{
  @Prop() readonly dense?: boolean
  @Prop() readonly room!: Room


  get isDaytime() 
  {
    return this.room.state.daytime?.is_daytime == "1"
  }

  get dayStart() 
  {   
    const startHour = Number(this.room.state.daytime?.start_hour)
    if (isNaN(startHour)) return null

    const start = this.$moment.utc().startOf('day').add(startHour, 'hours')
    if (start.unix() > this.$root.$data.unixTime) start.subtract(1, 'days')

    return start.unix()
  }

  get dayDuration() 
  {
    let hours = Number(this.room.state.daytime?.end_hour) - Number(this.room.state.daytime?.start_hour)
    if (isNaN(hours)) return null
    if (hours < 0) hours += 24    
    return hours*60*60
  }

  get nightDuration() 
  {
    if (!this.dayDuration) return null
    return (60*60*24) - this.dayDuration
  }

  get nightStart() 
  {
    const endHour = Number(this.room.state.daytime?.end_hour)
    if (isNaN(endHour)) return null

    const start = this.$moment.utc().startOf('day').add(endHour, 'hours')
    if (start.unix() > this.$root.$data.unixTime) start.subtract(1, 'days')

    return start.unix()
  }
}
