

import { Component, Vue, Provide } from 'vue-property-decorator';
import { connect, MqttClient } from 'mqtt'
import { MQTTConnection, Room, Device} from './types';
import Alert from './model/Alert';

interface MQTTProperty
{
  connections: MQTTConnection[];
  db: object;
  client: MqttClient | null;
  connected: boolean;
  connecting: boolean;
}


@Component({})
export default class Root extends Vue {

  // data
  drawer = null
  unixTime = NaN
  // unixTime = new Date().getTime()/1000|0
  
  mqtt: MQTTProperty = {
    connections: [],
    db: {},
    client: null,
    connected: false,
    connecting: true
  }

  devices: { [key: string]: Device } = {}
  rooms: Room[] = []
  alerts: Alert[] = []
 
  
  created () {

    // now
    // setInterval(()=> this.unixTime = new Date().getTime()/1000|0, 1000)
    setInterval(()=> this.unixTime += 1, 1000)

    // connection
    this.mqtt.connections = JSON.parse(localStorage.connections || "[]")

    
    const connection = this.mqtt.connections[0]

    console.log('connection', connection)
    if (!connection) {
      return
    }

    this.mqttConnect(connection)

    // default route
    if (this.$route.fullPath == "/")
    this.$router.replace({name:'dashboard'})
  }

  mqttConnect(connection: MQTTConnection) 
  {

    if (this.mqtt.client)
    {
      this.mqtt.client.end()
      this.rooms = []
      this.devices = {}
      this.mqtt.db = {}
    }

    const protocol = connection.ssl ? 'wss' : 'ws'
    const url = `${protocol}://${connection.host}:${connection.port}`
  
    this.$toast.default(`Connection to ${url}`)

    const client = connect(url, {
      username: connection.user,
      password: connection.pass,
      reconnectPeriod: 1000 * 3
    })

    this.mqtt.client = client
    
    client.subscribe("florafi/#")

    client.on('connect', () => {
      
      this.$toast.success("Connected")
      
      this.mqtt.connected = true
      this.mqtt.connecting = false
    })
      
    client.on('reconnect', () => {
      this.mqtt.connecting = true
    })

    client.on('offline', () => {
      this.$toast.warning("Offline")
      this.mqtt.connected = this.mqtt.connecting = false
      this.mqtt.db = {}
    })
    
    client.on('error', (e) => {
      console.error("mqtt error", e)
      this.mqtt.connected =this.mqtt.connecting = false
      this.$toast.error(e.toString())
    })
    
    client.on('message', (topic, data, packet) => {
      
      this.mqtt.connecting = false
      this.processMessage(topic, String(data), packet)
    })
    
  }



  processMessage(topic: string, msg: string, packet: any) 
  { 
    // console.log(topic, msg)

    // retain msg?
    if (packet.payload.length == 0)   
      this.$delete(this.mqtt.db, topic)
    else
      this.$set(this.mqtt.db, topic, msg)

    // room topic
    let match = topic.match(/^florafi\/room\/([\w_-]+)/)
    if (match) {
      let roomName = match[1]
      let subtopic = topic.substring("florafi/room/".length + roomName.length + 1)
      this.processRoomMessage(roomName, subtopic, msg, packet)
      return
    }

    // homie device message
    match = topic.match(/^homie\/(\w+)\/(.*)/)
    if (match) {

      const id = match[1]
      const subtopic = match[2]
      const device = this.devices[id]

      if (!device) 
      {
        console.error("Got homie message from unknown device! (%s)", id)
        return
      }

      // special case: ota status
      if (subtopic == "$implementation/ota/status")
      {
        if (!device['ota_status']) return
        let status = device.ota_status

        if (msg.match(/^\d$/))
          status.code = Number(msg)
        else if (msg.match(/^206 (\d+)\/(\d+)/))
        {
          const [elapsed, total] = msg.substring(4).split('/')
          // status.code = 206
          status.progress = Number(elapsed) / Number(total) * 100
          console.log("ota progress", status.progress)
        }

      }

      // console.log("device %s: %s -> %s", id, subtopic, msg)
      this.inflateMessage(device, subtopic, msg)
      if (subtopic.match(/implementation\/config$/))
        this.gcDevice(device)
    }
  }

  processRoomMessage(roomId: string, subtopic: string, msg: string, packet: any) {
    let room = this.rooms.filter(room => room.id == roomId)[0]

    // 1st time room
    if (!room) 
    {
      // console.log("new room", roomId, subtopic, msg)
      room = {
        id: roomId,
        deviceCount: 0,
        state: {},
        control: {},
        device: {}
      }

      this.rooms.push(room)
    }

    // alert message
    if (subtopic.match(/^alert\//))
    {
      const [type, name] = subtopic.split('/').splice(1)
      let alert = this.alerts.filter(a => a.roomId == room.id && a.type == type && a.name == name)[0]
      if (!alert) 
      {
        alert = new Alert(room.id, type, name)
        this.alerts.push(alert)
      }

      alert.processMsg(msg)
      return
    }

    // update current time
    const updatedTime = Number(msg)
    if (subtopic == "state/daytime/current_time" && !packet.retain && !isNaN(updatedTime)) 
    {
      // console.log("updating current time", updatedTime, this.unixTime - updatedTime, room.id)
      if (!this.unixTime || Math.abs(this.unixTime - updatedTime) > 2)
        this.unixTime = updatedTime
    }

    // device message
    if (subtopic.match(/^device\//))
    {
      this.processRoomDeviceMessage(room, subtopic, msg)
      return
    }

    // inflate message into room
    this.inflateMessage(room, subtopic, msg)
  }


  inflateMessage(target: object, topic: string, message: string) {
      
    const parts = topic.split(/\//)
    // console.log(topic, parts)
    let next_level = target as any
    for (let i = 0; i < parts.length; i++)
    {
      // remove leading '$', convert non-alphanumeric to underscore
      const part = parts[i].replace(/^\$/, "").replace(/\W/g, '_')

      // last, set value
      if (i+1 == parts.length)
      {
        // parse config, extract roomid
        if (part == "config" && message.match(/^{/))
        {
          const config = JSON.parse(message)
          // console.log('device config', target['id'], config)
          if (!config.settings)
            config.settings = {}

          this.$set(target, 'roomid', config.settings.garden_room)
          this.$set(next_level, part, config)
        }
        else
        {
          this.$set(next_level, part, message)
        }
      }
      // middle, traverse
      else
      {
        if (!next_level[part])
          this.$set(next_level, part, {})

        next_level = next_level[part]          
      }
    }

    // console.log("target", target)
  }


  processRoomDeviceMessage(room: Room, subtopic: string, deviceId: string) {

    let deviceRole = subtopic.split("/")[1].replace(/\W/g, '_')
    // console.log("%s.%s -> %s", room.id, deviceRole, deviceId )
    
    // null message, remove room device
    if (deviceId == "")
    {
      this.$delete(room.device, deviceRole)
      return
    }
    
    // parse device
    let device = this.devices[deviceId]

    // 1st time seen
    if (!device) 
    {
      device = { 
        id: deviceId,
        fw: {},
        implementation: {}
      }
      // console.log(`device ${deviceId}`, room.id, subtopic)
      

      this.$set(this.devices, deviceId, device)
      // this.$set(room.device, device.type, device)

      // subscribe to device state
      if (this.mqtt.client) 
      {
        this.mqtt.client.subscribe(`homie/${deviceId}/$state`)
        this.mqtt.client.subscribe(`homie/${deviceId}/$localip`)
        this.mqtt.client.subscribe(`homie/${deviceId}/$mac`)
        this.mqtt.client.subscribe(`homie/${deviceId}/$stats/+`)
        this.mqtt.client.subscribe(`homie/${deviceId}/$fw/+`)
        this.mqtt.client.subscribe(`homie/${deviceId}/$implementation/+`)
        this.mqtt.client.subscribe(`homie/${deviceId}/$implementation/ota/status`)
      }
    }

    // device not of this room
    if (device.roomid && device.roomid != room.id)
      return

    // add room device
    // console.log("+1 room device", room.id, deviceId)
    room.deviceCount += 1

    if (typeof room.device == "undefined")
      this.$set(room, 'device', {})
    
    this.$set(room.device, deviceRole, device)
  }

  gcDevice(device: Device) {

    if (!device.roomid)
    {
      // console.log("gcdrvice skipped: no roomid")
      return
    }

    // handle device changed room
    this.rooms.forEach( room => {

      // skip current device room, skip room without devices
      if (room.id == device.roomid || !room.device) return


      for (let [deviceRole, roomDevice] of Object.entries(room.device) ) 
      {
        if (roomDevice.id == device.id)
        {
          let topic = `florafi/room/${room.id}/device/${deviceRole.replace(/_/g,'-')}`
          // console.log(`deleting bad ref: ${topic}`)
          this.mqttPublish(topic, null, true)
          this.$delete(room.device, deviceRole)
          room.deviceCount--
        }          
      }
    })
  }

  @Provide('publishControl')
  publishRoomControl(room: Room, endpoint: string, payload: any) {

    let [device, control] = endpoint.split(".")

    // invalid room control
    if (!(room.control && room.control[device] && room.control[device][control])) 
    {
      console.error("Invalid room '%s' control '%s'", room.id, endpoint)
      return
    }

    // send payload
    this.$toast.info(`${endpoint} >>> ${payload}`)
    this.mqttPublish(room.control[device][control], payload)
  }

  publishRoom(roomId: string, subtopic: string, payload: any, retain: boolean) {
    this.mqttPublish(`florafi/room/${roomId}/${subtopic}`, payload, retain)
  }

  @Provide()
  mqttPublish(topic: string, payload: string|number|Buffer|null, retain = false) {
    console.log("mqttPublish()", topic, payload, retain)
    if (typeof payload == "number")
      payload = String(payload)

    if (payload == null)
      payload = new Buffer([])
    
    if (this.mqtt.client)
      this.mqtt.client.publish(topic, payload, { qos: 0, retain })
  }

}
