





























































import { Component, Vue, Prop } from 'vue-property-decorator'
import { Room } from '../../types'

import colors from 'vuetify/lib/util/colors'

@Component({})
export default class EnvironmentOverview extends Vue  
{

  @Prop() readonly room!: Room

  get deviceOnline() {
    let device = this.room.device["ipcc"]
    if (!device) return false
    return device.state == 'ready'
  }

  get temperature() {
    return this.room.state.temperature?.last_value 
    ? this.room.state.temperature?.last_value + 'º'
    : '-'
  }

  get idealTemperature() {
    return this.room.state.temperature?.ideal 
    ? this.room.state.temperature?.ideal + 'º'
    : null
  }

  get temperatureIndex() {
    return this.room.state.temperature?.index == null
      ? NaN : Number(this.room.state.temperature?.index)
  }

  get temperatureIndexLabel() {
    let label = ''
    switch (this.temperatureIndex) {
      case -1: label = 'Baixa'; break;
      case -2: label = 'Muito baixa'; break;
      case 1:  label = 'Alta'; break;
      case 2:  label = 'Muito alta'; break;
    }
    return label      
  }

  get temperatureColor() {
    if (isNaN(this.temperatureIndex)) return ''    
    let absIndex = Math.abs(Number(this.room.state.temperature?.index))
    if (absIndex == 2) return colors.red.base
    else if (absIndex == 1) return colors.orange.base
    else return ''    
  }


  get humidity() {
    return this.room.state.humidity?.last_value 
    ? this.room.state.humidity?.last_value + '%'
    : '-'
  }
  get idealHumidity() {
    return this.room.state.humidity?.ideal 
    ? this.room.state.humidity?.ideal + '%'
    : null
  }

  get humidityIndex() {
    return this.room.state.humidity?.index == null
      ? NaN : Number(this.room.state.humidity?.index)
  }

  get humidityIndexLabel() {
    let label = ''
    switch (this.humidityIndex) {
      case -1: label = 'Baixa'; break;
      case -2: label = 'Muito baixa'; break;
      case 1:  label = 'Alta'; break;
      case 2:  label = 'Muito alta'; break;
    }
    return label      
  }

  get humidityColor() {
    if (isNaN(this.humidityIndex)) return ''    
    let absIndex = Math.abs(Number(this.room.state.humidity?.index))
    if (absIndex == 2) return colors.red.base
    else if (absIndex == 1) return colors.orange.base
    else return ''    
  }
  


}

