
import { Component, Vue, Prop } from 'vue-property-decorator';
import { RelayState } from '../../types'

@Component({})
export default class RelayBase extends  Vue
{

  @Prop() readonly label?: string
  @Prop() readonly name!: string
  @Prop() readonly state!: RelayState

  // @Inject() publishControl!: Function

  get isDeviceOnline() {
    return true
  }

  get isOn() {
    return this.state?.power == "ON"
  }

  get automationEnabled() {
    return this.state?.automation == "ON"
  }

}