
<template>

  <div>
    <!-- toolbar -->
    <v-app-bar app clipped-left>
      <v-app-bar-nav-icon @click.stop="toggleDrawer" />
      <v-toolbar-title>Devices</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn @click="checkFirmwareUpdates">Check updates</v-btn>
    </v-app-bar>

    <!-- section content -->
    <v-container :class="{ 'pt-0': $vuetify.breakpoint.xsOnly, 'pl-0': $vuetify.breakpoint.xsOnly, 'pr-0': $vuetify.breakpoint.xsOnly }">
      <device-list></device-list>
    </v-container>
    
  </div>
</template>

<script>

import DeviceList from "@/components/device/DeviceList"
const md5 = require('js-md5')

export default {
  
  inject: ['toggleDrawer'],
  components: {
    DeviceList
  },
  data: () => ({

    firmware: [
      {
        name: 'florafi-ipcc',
        regex: /ipcc/,
        url: "/firmware/ipcc.bin",
        size: null,
        md5: null
      },
      {
        name: 'florafi-ebbflow',
        regex: /ebbflow/,
        url: "/firmware/ebbflow.bin",
        size: null,
        md5: null
      }
    ]
    
  }),
  methods: {
    checkFirmwareUpdates() {
      this.$data.firmware.forEach(fw => {
        let devices = Object.values(this.$root.devices).filter(d => d.fw && d.fw.name.match(fw.regex))

        console.log("checking firmware %s", fw.name, devices)
        if (devices == 0)
        {
          console.log(" - no devices using this firmware, skipping.")
          return
        }

        // fetch lastest
        this.$http
          .get(fw.url, {responseType: 'arraybuffer'})
          .then(res => res.data)
          .then(buf => 
          {
            // save data, and calculate md5
            fw.buf = buf
            fw.data = new Uint8Array(buf)

            const hash = md5.create()
            hash.update(buf)
            fw.md5 = hash.hex()      
            
            console.log(fw)
            
            devices.forEach(dev => {
              this.$set(dev, 'latest_fw', fw)
            })
          })
      })

    }
  },
  computed: {
    deviceList() {      
      let devices = Object.values(this.$root.devices)
      devices.sort((a,b) => a.id < b.id ? -1 : 1)
      // console.log(devices[0])
      return devices
    }
  }
}
</script>


