





























import { Component, Vue, Prop } from 'vue-property-decorator'
import { Device } from '../../types'
import DeviceSignalIcon from './DeviceSignalIcon.vue'
import DeviceLink from '../device/DeviceLink.vue'



@Component({
  components: {
    DeviceSignalIcon,
    DeviceLink
  }
})
export default class DeviceStatusListItem extends Vue {
    @Prop() readonly device!: Device

    get deviceName() {     
        return this.device.implementation?.config?.name
    }
}

