




























import { Vue, Component, Prop } from 'vue-property-decorator';


interface SettingMenuItem {
  id: string;
  title: string;
  icon: string;
  device: string;
}

@Component({
  components: {
    
  }
})
export default class EbbflowSettings extends Vue
{
  @Prop() dense?: boolean
  
  @Prop() label!: string
  @Prop() value!: string
  @Prop() icon?: string
  @Prop() description?: string
  @Prop() type?: string
  @Prop() format?: Function

  get formattedValue() {
    return this.format ? this.format(this.value) : this.value
  }

  get hasAction() {
    return false
  }


}
