
import { Component, Vue, Prop, Inject } from 'vue-property-decorator';
import { Room } from '../../types'
import { strictEqual } from 'assert';

@Component({})
export default class IrrigationBase extends Vue 
{
  @Prop() readonly room!: Room
  
  // data
  

  // methods
  @Inject() publishControl!: Function

  enableAutomation() {
    this.publishControl(this.room, 'interval_irrigation.automation', "ON")
  }

  // computed  
  get automationDisabled() {
    return this.room.state.interval_irrigation?.automation == "OFF"
  }

  get powerON() {
    return this.room.state.interval_irrigation?.power == "ON"
  }

  get deviceOnline() {
    let device = this.room.device["interval_irrigation"]
    if (!device) return false
    return device.state == 'ready'
  }

  get isDaytime() {
    let state = this.room.state   
    return state.daytime && typeof state.daytime.is_daytime == "string"
      ? state.daytime.is_daytime == "1"
      : true
  }

  get lastON() {
    return Number(this.room.state.interval_irrigation?.last_on)
  }
  get lastOFF() {
    return Number(this.room.state.interval_irrigation?.last_off)
  }

  get elapsedON() {
    let state = this.room.state        
    if (!(state.interval_irrigation && state.interval_irrigation.last_on)) return null
    let now = this.$root.$data.unixTime
    let seconds = now - Number(state.interval_irrigation.last_on)
    return seconds
  }

  get elapsedOFF() {
    let state = this.room.state        
    if (!(state.interval_irrigation && state.interval_irrigation.last_off)) return null
    let now = this.$root.$data.unixTime
    let seconds = now - Number(state.interval_irrigation.last_off)
    return seconds
  }

  get intervalON() {
    let state = this.room.state  
    if (!(state.interval_irrigation && state.interval_irrigation.duration)) return null
    let seconds = Number(state.interval_irrigation.duration)
    if (state.interval_irrigation.duration_unit == "m")
      seconds = seconds * 60
    return seconds
  }

  get intervalOFF() {
    let state = this.room.state
    let minutes = this.isDaytime
      ? state.interval_irrigation?.day_interval
      : state.interval_irrigation?.night_interval
    
    if (typeof minutes == "undefined") return null
    return Number(minutes)*60
  }

  get progressON() {
    let state = this.room.state 
    let seconds_elapsed = this.elapsedON
    if (seconds_elapsed == null) return null
    if (!(state.interval_irrigation && state.interval_irrigation.power == "ON")) return null

    if (typeof state.interval_irrigation.duration == "undefined") return null
    let total_seconds = Number(state.interval_irrigation.duration)

    if (state.interval_irrigation.duration_unit == "m")
      total_seconds *= 60

    let progress = seconds_elapsed / total_seconds * 100 |0
    return progress
  }

  get progressOFF() {
    let state = this.room.state        
    let seconds_elapsed = this.elapsedOFF
    if (seconds_elapsed == null) return null
    if (!(state.interval_irrigation && state.interval_irrigation.power == "OFF")) return null

    let total_seconds = Number(state.interval_irrigation[this.isDaytime ? 'day_interval' : 'night_interval'])

    if (typeof total_seconds == "undefined") return null
    total_seconds *= 60
    
    let progress = seconds_elapsed / total_seconds * 100 |0
    return progress
  }

  get countdownON() {
    if (this.intervalON == null || this.elapsedON == null) return null
    return this.$moment.duration(this.intervalON - this.elapsedON , 'seconds') 
  }

  get countdownOFF() {
    if (this.intervalOFF == null || this.elapsedOFF == null)  return null
    return this.$moment.duration(this.intervalOFF - this.elapsedOFF, 'seconds')
  } 
}
