
import { Component, Vue, Prop, Inject } from 'vue-property-decorator'
import { Room } from '../../types'

import colors from 'vuetify/lib/util/colors'

@Component({})
export default class LightOverview extends Vue  {

  @Prop() readonly room!: Room
  @Inject() publishControl!: Function

  lastCurrentTime = NaN
  timeOffset = 0

  get device() {
    return this.room.device.ipcc
  }

  get lightRelay() {
    return this.room.device.light_relay
  }

  get lightRelayOnline() {
    return this.lightRelay?.state == 'ready'
  }

  get deviceOnline() {
    return this.device?.state == 'ready'
  }

  get lightAutomationEnabled() {
    return this.room.state.light_relay?.automation == 'ON'
  }
  set lightAutomationEnabled(enabled: boolean) {
    this.publishControl(this.room, 'light_relay.automation', enabled ? 'ON' : 'OFF')
  }

  get currentTime() {
      if (isNaN(this.lastCurrentTime)) return null     
      return this.$moment.unix(this.lastCurrentTime + this.timeOffset)
  }

  get currentTimeUTC() {
      if (!this.currentTime) return null
      return this.currentTime.clone().utc()
  }

  get isDaytime() {
    let state = this.room.state   
    return typeof state.daytime?.is_daytime == "string"
      ? state.daytime?.is_daytime == "1"
      : true
  }

  get intensityColor() {
    let intensity = Number(this.room.state.light_intensity)
    let color = ''

    if (this.isDaytime) 
    {
      if (intensity < 95) color = colors.orange.base
      if (intensity < 85) color = colors.red.base
    }
    else
    {
      if (intensity > 1) color = colors.orange.base
      if (intensity > 5) color = colors.red.base
    }

    // console.log('insensity color:', color)
    return color
  }

  get startHour() {
    let daytime = this.room.state.daytime
    return typeof daytime?.start_hour == "undefined" ? null : Number(daytime.start_hour)
  }

  get endHour() {
    let daytime = this.room.state.daytime
    return typeof daytime?.end_hour == "undefined" ? null : Number(daytime.end_hour)
  }
 
  get dayLength() {
    if (this.startHour == null || this.endHour == null) return null

    let duration = (this.endHour - this.startHour) * 60 * 60

    return duration > 0
      ? duration
      : duration + (60*60*24)
  }

  get nightLength() {
    if (!this.dayLength) return null
    return (60*60*24) - this.dayLength
  }

  get dayElapsed() {
    if (this.startHour == null || this.endHour == null) return null
    
    const now = this.$root.$data.unixTime
    const start_time = this.$moment.utc().startOf('day').add(this.startHour, 'hours')
    // console.log(now, start_time.unix(), now - start_time.unix())
    if (start_time.unix() > now)
      start_time.subtract(1, 'days')

    return now - start_time.unix()
  }

  get nightElapsed() {
    if (this.endHour == null) return null
    
    let epoch = this.$moment.utc().startOf('day').add(this.endHour, 'hours')
    // if (this.startHour > this.endHour)
    //   start_time.subtract(1, 'days')

    return this.$root.$data.unixTime - epoch.unix()
  }

  get dayProgress() {
    if (this.dayElapsed == null || this.dayLength == null) return null
    return this.dayElapsed / this.dayLength * 100 |0
  }

  get nightProgress() {
    
    if (this.nightElapsed == null || this.nightLength == null) return null
    // console.log(this.nightLength, this.nightElapsed)
    return this.nightElapsed / this.nightLength * 100 |0
  }

  get countdown() {
    
    let length  = this.isDaytime ? this.dayLength : this.nightLength 
    let elapsed = this.isDaytime ? this.dayElapsed : this.nightElapsed
    // console.log(length, elapsed)
    if (length == null || elapsed == null) return null
    return this.$moment.duration(length - elapsed, 'seconds')
  }
  
}