<template>
  <v-app id="inspire">
    <!-- <v-app-bar app color="" dark flat clipped-left dense>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title>Flora-Fi</v-toolbar-title>     
      <v-spacer></v-spacer> 
    </v-app-bar> -->
    
    <v-navigation-drawer v-model="drawer" app >
      <v-list dense>

        <v-subheader>Menu</v-subheader>

        <v-list-item to="/dashboard">
          <v-list-item-action>
            <v-icon>mdi-view-dashboard</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Visão Geral</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-list-item link v-for="room in rooms" :key="room.id" :to="'/room/'+room.id">
          <v-list-item-action>
            <v-icon>mdi-home</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>{{room.name || room.id}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-divider/>

        <v-list-item to="/devices">
          <v-list-item-action>
            <v-icon>mdi-cog</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              Dispositivos
              <v-badge color="red" class="ml-1" :value="offlineDevices > 0" :content="offlineDevices" :title="`${offlineDevices} offline`"></v-badge>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-list-item to="/settings">
          <v-list-item-action>
            <v-icon>mdi-cog</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Configurações</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-main>
      
      <router-view></router-view>

    </v-main>

    <!-- dialogs -->
    <prompt-dialog ref="promptDialog"></prompt-dialog>


  </v-app>
</template>



<style lang="scss">

.v-application a.muted {
  text-decoration: none;
  color: inherit;
  &:hover {
    text-decoration: underline;
  }
}

</style>


<script>

  import PromptDialog from "./components/common/PromptDialog"

  export default {
    props: {
      // source: String,
    },
    components: {
      PromptDialog
    },
    data: () => ({
      drawer: null
    }), 
    created() {
      // console.log("app component", this)
    },
    provide() {
      return {
        prompt: this.prompt,
        toggleDrawer: this.toggleDrawer
      }
    },
    methods: {

      prompt(...args) {
        let dialog = this.$refs.promptDialog
        return dialog.open(...args)
      },

      toggleDrawer() {
        this.drawer = !this.drawer
      }
      
    },
    computed: {
      rooms() {
        let rooms = this.$root.rooms.filter( (r)=> {
          return r.id != "waiting" && r.deviceCount > 0
        })
        rooms.sort((a,b) => a.id < b.id ? -1 : 1)
        return rooms
      },
      offlineDevices() {
        let count = 0

        Object.values(this.$root.devices).forEach(dev => {
          if (dev.state && dev.state != "ready")
           count++
        })

        return count
      }
    }
  }
</script>
