






















import { Component, Vue, Prop, Inject } from 'vue-property-decorator';
import { Room, RoomState } from '../../types'
import RelayListItem from './RelayListItem.vue'


interface RelayConfig {
  name: keyof RoomState;
  title: string;
}


@Component({
  components: {
    RelayListItem
  }
})
export default class RelayList extends  Vue
{

  @Prop() readonly room!: Room
  @Prop() readonly dense?: boolean
  @Prop() title?: string
  @Inject() publishControl!: Function


  allRelays: RelayConfig[] = [
    { name: 'light_relay', title: 'Iluminação' },
    { name: 'interval_irrigation', title: 'Irrigação' },
    { name: 'ebbflow_flood_relay', title: 'Bomba Flood' },
    { name: 'ebbflow_drain_relay', title: 'Bomba Drain' },
    { name: 'humidifier_relay', title: 'Umidificador' }
  ]

  
  get relays() {
    return this.allRelays.filter( (r: RelayConfig) => this.room.device[r.name] && this.room.state[r.name])
  }

  onSetControl(control: string, value: string) {
    // console.log('on RelayList onSetControl()', control, value)
    this.publishControl(this.room, control, value)
  }


}
