

export default class Alert
{

  roomId: string
  type: string
  name: string
  timestamp = NaN


  constructor(roomid: string, type: string, name: string)
  {
    this.roomId = roomid
    this.type = type
    this.name = name    
    // console.log('new alert', this, this.isActive())
  }

  processMsg(msg: string) {
    this.timestamp = parseInt(msg)
  }

  isActive() {
    return !isNaN(this.timestamp)
  }
}