


















































import { Vue, Component, Prop } from 'vue-property-decorator';
import { Room } from "@/types"
import EbbflowSettings from "@/components/ebbflow/EbbflowSettings.vue";
import LightSettings from "@/components/light/LightSettings.vue";
import TemperatureSettings from "@/components/temperature/TemperatureSettings.vue";
import IrrigationSettings from "@/components/irrigation/IrrigationSettings.vue";

interface SettingMenuItem {
  id: string;
  title: string;
  icon: string;
  device: string;
  component: string;
}

@Component({
  components: {
    EbbflowSettings,
    TemperatureSettings,
    IrrigationSettings,
    LightSettings
  }
})
export default class RoomSettings extends Vue
{
  @Prop() dense?: boolean
  @Prop() title?: string
  @Prop() readonly room!: Room

  activeWindow = 0
  windows: SettingMenuItem[] = []

  allSections: SettingMenuItem[] = [
    { id: 'lighting',    icon: 'mdi-lightbulb-on',  title: 'Iluminação',  component: 'LightSettings', device: 'ipcc' },
    { id: 'temperature', icon: 'mdi-thermometer',   title: 'Temperatura', component: 'TemperatureSettings', device: 'ipcc' },
    { id: 'humidity',    icon: 'mdi-water-percent', title: 'Umidade',     component: 'HumiditySettings', device: 'ipcc' },
    { id: 'ebbflow',     icon: 'mdi-water',         title: 'Ebbflow',     component: 'EbbflowSettings', device: 'ebbflow' },
    { id: 'irrigation',  icon: 'mdi-water',         title: 'Irrigação',   component: 'IrrigationSettings', device: 'interval_irrigation' },
  ]

  created() {
    // prune this.windows on window change
    // NOTE: this is a workaround to replace the broken? v-window 'change' event
    this.$watch('activeWindow', (i: number) => {
      // console.log('window changed to ', from, to)
      if (i < this.windows.length) 
        setTimeout(()=>this.windows.splice(i), 750)
    })
  }

  get sections() {
    return this.allSections.filter( (s: SettingMenuItem) => !!this.room.device[s.device])
  }

  pushWindow(section: SettingMenuItem)
  {
    // console.log("opening section", section)
    this.windows.push(section)
  }

  popWindow() {
    if (this.activeWindow == 0) return
    this.activeWindow -= 1
    // console.log("closed section", this.activeWindow, this.windows)
  }




}
