

























































































































import { Component } from 'vue-property-decorator'
import LightBase from './LightBase'
import RelayControlList from '../common/RelayControlList.vue'
import DeviceStatusListItem from '../common/DeviceStatusListItem.vue'

@Component({
  components: {
    RelayControlList,
    DeviceStatusListItem
  }
})
export default class LightControl extends LightBase {

    created() {
        this.$watch('room.state.daytime.current_time', (time) => {
            console.log('new current_time', time)
            this.lastCurrentTime = parseInt(time)
            this.timeOffset = 0
        })

        // count seconds
        setInterval(() => { this.timeOffset++ }, 1000)
    }
}

