




































































































import { Component, Vue, Prop } from 'vue-property-decorator';
import { EnvironmentSensorState } from "@/types"
import IncrementButtons from "@/components/common/IncrementButtons.vue"

@Component({
    components: {
        IncrementButtons
    }
})
export default class IdealRangeControl extends Vue {
    
    @Prop() value!: EnvironmentSensorState;
    @Prop({default: ''}) suffix?: string

    created() {
        console.log("value", this.value)
    }

    setControl(key: string, value: number) {
        console.log(key, value)
    }

    get ideal() {
        return this.value.ideal == null ? null : Number(this.value.ideal)
    }

    get idealMin() {
        if (this.ideal == null || this.value.ideal_min == null) return null
        return this.ideal + Number(this.value.ideal_min)
    }

    get idealMax() {
        if (this.ideal == null || this.value.ideal_max == null) return null
        return this.ideal + Number(this.value.ideal_max)
    }

    get alertMin() {
        if (this.idealMin == null || this.value.alert_min == null) return null
        return this.idealMin + Number(this.value.alert_min)
    }

    get alertMax() {
        if (this.idealMax == null || this.value.alert_max == null) return null
        return this.idealMax + Number(this.value.alert_max)
    }

    

}

