

























































import { Vue, Component, Prop } from 'vue-property-decorator';

@Component({
  components: {
    
  }
})
export default class ProgressListItem extends Vue
{
  @Prop() readonly inactivity?: boolean
  @Prop() readonly dense?: boolean
  @Prop() readonly forward?: boolean
  @Prop() readonly icon!: string
  @Prop() readonly title!: string
  @Prop() readonly subtitle!: string

  @Prop() readonly duration?: number
  @Prop() readonly start!: number

  get hasDuration() {
    return typeof this.duration == "number"
  }

  get elapsedSeconds() {
    return this.$root.$data.unixTime - this.start
  }
  
  get remainingSeconds() {
    if (!this.duration) return NaN
    // console.log('remaining seconds', this.duration, this.elapsedSeconds, this.duration - this.elapsedSeconds, this.elapsedSeconds - this.duration)
    return this.elapsedSeconds - this.duration
    // return this.duration - this.elapsedSeconds
  }
  
  get progress() {
    if (!this.duration) return NaN
    const progress = this.elapsedSeconds / this.duration * 100
    // console.log('progress', progress)
    return progress
  }

}
