<template>
  <v-card>
    <v-card-title>
      <v-btn @click="addConnection">Nova conexão</v-btn>
      <v-btn color="primary" @click="save" v-show="isDirty">Salvar</v-btn>
    </v-card-title>
    <v-list>
      <template v-for="(conn, index) in connections">

        <v-list-item :key="index">

          <v-list-item-icon>
            <v-icon :large="!$vuetify.breakpoint.xs">{{ conn.editMode ? "mdi-pencil" : "mdi-lan-connect" }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>

            <v-row v-if="conn.editMode" dense>
              <v-col cols="12" sm=8>
                <v-text-field v-model="conn.host" @change="conn.isDirty = true" label="IP ou Hostname" hint=""></v-text-field> 
              </v-col>
              <v-col cols="12" sm=4>
                <v-text-field v-model="conn.port" @change="conn.isDirty = true" label="Porta" type="number" hint=""></v-text-field> 
              </v-col>
              <v-col cols="12">
                <v-switch v-model="conn.ssl" label="SSL"></v-switch>
              </v-col>
              <v-col cols="12">
                <v-switch v-model="conn.auth" label="Possui autenticação"></v-switch>
              </v-col>
              <v-col cols="12" sm=6 v-if="conn.auth">
                <v-text-field v-model="conn.user" @change="conn.isDirty = true" label="Usuário" hint="(opcional)"></v-text-field> 
              </v-col>
              <v-col cols="12" sm=6 v-if="conn.auth">
                <v-text-field v-model="conn.pass" @change="conn.isDirty = true" label="Senha" hint=""></v-text-field> 
              </v-col>
              <v-col cols="12">
                <v-switch v-model="conn.default" label="Conectar automaticamente"></v-switch>
              </v-col>
              <v-col>
                <v-btn @click="finishEdit(conn)" color="primary">OK</v-btn>
              </v-col>
            </v-row>

            <v-list-item-title v-else>
              {{ conn.ssl ? 'wss' : 'ws' }}://{{conn.host}}:{{conn.port}}
              <v-icon small v-if="conn.user" :title="'Username: ' + conn.user ">mdi-account</v-icon>
              <v-icon small v-if="conn.user" :title="'Password: ' + conn.user ">mdi-key</v-icon>
            </v-list-item-title>

          </v-list-item-content>

          <v-list-item-icon v-if="conn.editMode">
          </v-list-item-icon>

          <v-list-item-icon v-else>
            <v-btn text @click="$root.mqttConnect(conn)"><v-icon >mdi-power-plug</v-icon></v-btn>
            <v-btn text @click="conn.editMode = true"><v-icon >mdi-pencil</v-icon></v-btn>
            <v-btn text @click="deleteConnection(index)"><v-icon>mdi-delete</v-icon></v-btn>
          </v-list-item-icon>
          
        </v-list-item>

        <v-divider  v-if="index+1 < connections.length" :key="'div'+index"></v-divider>

      </template>
    </v-list>
  </v-card>
</template>

<script>

  export default {
    name: 'ConnectionSettings',
    props: [],
    components: {
    },
    data: () => ({
      connections: [],
      isDirty: false,
    }),
    mounted() {
      this.$data.connections = JSON.parse(JSON.stringify(this.$root.mqtt.connections)).map( conn => {conn.editMode = false; return conn} )
      console.log("conn", this.connections)
    },
    methods: {
      addConnection() {
        const newConnection = { isNew: true, editMode: true }
        this.connections.push(newConnection)
        console.log("new connection", newConnection)
      },
      deleteConnection(i) {
        if (!this.connections[i].isNew)
          this.isDirty = true

        this.connections.splice(i, 1)
      },
      finishEdit(conn) {
        conn.editMode = false
        if (conn.isDirty)
          this.isDirty = true
      },
      save() {
        
        this.connections.forEach( conn => {
          delete conn['editMode']
          delete conn['isNew']
          delete conn['isDirty']
        })

        this.$root.mqtt.connections = this.connections
        localStorage.connections = JSON.stringify(this.connections)
        this.isDirty = false
        console.log(localStorage.connections)
      }
    }
  }
</script>


