      <v-btn v-else icon @click="$router.back()"><v-icon>mdi-arrow-left</v-icon></v-btn>
      <v-toolbar-title>
        {{room.name || room.id}} 
        <v-icon small>mdi-chevron-double-right</v-icon> 
        {{sectionTitle}}
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <!-- <v-btn icon @click="drawer = !drawer"><v-icon>mdi-menu</v-icon></v-btn> -->
      <!-- menu -->
      <!-- <v-select  hide-details filled dense :style="{flex: $vuetify.breakpoint.xsOnly ? 2 : 0.8}"
        menu-props="light"
