        :items="sections"
        @change="changeSection">
      </v-select> -->

    </v-app-bar>


    <!-- section content -->
    <v-container :class="{ 'pb-0': true, 'pt-0': $vuetify.breakpoint.xsOnly, 'pl-0': $vuetify.breakpoint.xsOnly, 'pr-0': $vuetify.breakpoint.xsOnly }">

      <room-overview v-if="section == 'overview'" :room="room"></room-overview>
      <ebbflow-panel v-else-if="section == 'ebbflow'" :room="room"></ebbflow-panel>
      <room-irrigation v-else-if="section == 'irrigation'" :room="room"></room-irrigation>
      <light-controls v-else-if="section == 'light'" :room="room"></light-controls>
      <temperature-control v-else-if="section == 'temperature'" :room="room"></temperature-control>
      <humidity-control v-else-if="section == 'humidity'" :room="room"></humidity-control>
      <cycle-controls v-else-if="section == 'cycle'" :room="room"></cycle-controls>
      <div v-else-if="section == 'devices'">
        <device-list :roomid="room.id"></device-list>
      </div>

      <!-- <v-navigation-drawer
        v-model="drawer"
        right="right"
        app
        clipped
        >
        <v-list dense>
        <v-subheader>{{room.name || room.id}}</v-subheader>
          <v-list-item-group v-model="drawerValue" color="primary">
            <v-list-item v-for="item in sections" :key="item.value" 
              
              :to="{name: 'room', params: { roomid: room.id, section: item.value }}"
              >
              <v-list-item-icon><v-icon v-if="item.icon" v-text="item.icon"></v-icon></v-list-item-icon>
              <v-list-item-content>{{item.text}}</v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>

        <div class="pl-4" style="position:absolute;bottom:0;width:100%;">
          <v-switch v-model="showAllSections" label="Mostrar todas as áreas"></v-switch>          
