    </v-container>


    
    
  </div>

  <div v-else>

  </div>

</template>

<script>
  // import Vue from 'vue'
  import RoomOverview from "@/components/room/Overview"
  import EbbflowPanel from "@/components/ebbflow/EbbflowPanel"
  import DeviceList from "@/components/device/DeviceList"
  import LightControls from "@/components/light/LightControls"
  // import CycleControls from "./CycleControls"
  // import HumidityControls from "./HumidityControls"
  import RoomIrrigation from "@/components/irrigation/Irrigation" 
  import TemperatureControl from "@/components/temperature/TemperatureControl.vue"
  import HumidityControl from "@/components/humidity/HumidityControl.vue"

  export default {
    name: 'RoomView',
    inject: ['toggleDrawer'],
    props: {
      roomid: { type: String },
      section: { type: String, default: 'overview' }
    },
    components: {
      RoomOverview,
      EbbflowPanel,
      DeviceList,
      // CycleControls,
      LightControls,
      TemperatureControl,
      HumidityControl,
      RoomIrrigation
    },
    data: () => ({
      tab: null,
      room: null,
      drawer: false,
      drawerValue: null,
      showAllSections: false,
      sections: [
        { text: 'Visão Geral', value: 'overview', icon: 'mdi-view-grid' },
        { text: 'Iluminação', value: 'light', icon: 'mdi-lightbulb-multiple-outline' },
        { text: 'Ebbflow', value: 'ebbflow', icon: 'mdi-state-machine' },
        { text: 'Irrigação', value: 'irrigation', icon: 'mdi-sprinkler-variant' },
        { text: 'Temperatura', value: 'temperature', icon: 'mdi-thermometer' },
        { text: 'Umidade', value: 'humidity', icon: 'mdi-water-percent' },
        { text: 'CO2', value: 'co2', icon: 'mdi-gas-cylinder' },
        { text: 'Ciclo', value: 'cycle', icon: 'mdi-calendar-clock' },
        { text: 'Dispositivos', value: 'devices', icon: 'mdi-signal' },
        { text: 'Alertas', value: 'alerts', icon: 'mdi-bell' },
      ]
    }),
    computed: {
      sectionTitle() {
        const section = this.sections.filter( sec => sec.value == this.section)[0]
        return section ? section.text : ''
      }
    },    
    methods: {
      updateRoom: function() {
        this.room = this.$root.rooms.filter(room => room.id == this.roomid)[0]
      },
      publishRoomControl(...args) {
        console.log(args)
      },
      changeSection(section){
        this.$router.push({
          name: 'room',
          params: {
            roomid: this.roomid,
            section: section
          }
        })
      }
    },
    created() {
      this.updateRoom()      
      this.drawerValue = this.section

      if (this.room) return

      let unwatch = this.$watch("$root.rooms", () => {
        // console.log("room changed")
        this.updateRoom()
        if (this.room) unwatch()
      })
    },
    watch: {
      $route() {
        this.updateRoom()        
      }
    }
  }
</script>


