













































































































import { Component, Vue, Prop, Inject, Ref } from 'vue-property-decorator'
import { Device, Room, HomieConfig } from '../../types'

import DeviceConfigDialog from "./ConfigDialog.vue"
import About from "./AboutDialog.vue"
import DeviceSignalIcon from "../common/DeviceSignalIcon.vue"


@Component({
    components: {
        DeviceSignalIcon,
        DeviceConfigDialog,
        About
    }
})
export default class DeviceList extends Vue {

    @Prop({ default: false }) dense?: boolean
    @Prop({ default: false }) hideRoomLink?: boolean
    @Prop() device!: Device
    @Inject() prompt!: Function
    @Inject() mqttPublish!: Function
    @Ref() readonly configDialog!: typeof DeviceConfigDialog


    itemUptime() {
        if (this.device.stats?.uptime == null) return null
        return this.$moment.duration(Number(this.device.stats.uptime), 's')
    }
    
    hasLatestFw() {
        return this.device?.latest_fw && this.device.fw && this.device.latest_fw.md5 == this.device.fw.checksum
    }
    
    isOnline() {
        return this.device?.state == 'ready'
    }
    
    openConfig() 
    {
        // if (this.device.state != "ready")
        // {
        //     this.$toast.error("O dispositivo está offline.")
        //     return
        // }

        // open dialog
        const dialog = this.$refs.configDialog as DeviceConfigDialog
        if (dialog == null) return

        const opts = this.$vuetify.breakpoint.mdAndDown ? {} : {
            fullScreen: false,
            width: 500
        }
        
        dialog.open(this.device, opts).then( (config: HomieConfig) => {

            console.log('saving confif', this.device, config)
            this.mqttPublish(`homie/${this.device.id}/$implementation/config/set`, JSON.stringify(config))
            this.$toast.success("Configuração enviada. O dispositivo riniciará com as novas configurações.")
        })
    } 

    forget() 
    {
        const room: Room = this.$root.$data.rooms.filter((r: Room) => r.id == this.device.roomid)[0]
        if (!room) return

        // delete room refs
        for ( let [deviceName, d] of Object.entries(room.device))
        {
            if (d.id != this.device.id) continue

            // delete mqtt entry
            const deviceTopic = `florafi/room/${room.id}/device/${deviceName.replace(/_/g, '-')}`
            this.mqttPublish(deviceTopic, null, true)                
        }

        // delete device reference
        this.$delete(this.$root.$data.devices,  this.device.id)
    }
    
    startOTA() {
        if (!this.device.latest_fw)
            return console.error("missing latest firmware for device %s", this.device.id)
        
        // publish
        let fw = this.device.latest_fw 
        this.mqttPublish(`homie/${this.device.id}/$implementation/ota/firmware/${fw.md5}`, fw.data)
        
        this.$set(this.device, 'ota_status', {
            progress: 0
        })
        
    }

    reboot() 
    {        
        this.mqttPublish(`homie/${this.device.id}/$implementation/config/set`, "{}")        
    }

}
