

















import { Component, Vue, Prop  } from 'vue-property-decorator'
// import { Room } from '@/types'
import Alert from '@/model/Alert'


@Component({
  components: {
  }
})

export default class AlertListItem extends Vue {

  @Prop() readonly alert!: Alert

  get title() {
    return this.alert.name
  }
  get icon() {
    const icons: {[key: string]: string} = {
      info: 'mdi-information-outline',
      warning: 'mdi-exclamation',
      error: 'mdi-information-octagon'

    }
    return icons[this.alert.type]
  }
  get elapsedTime() {
    const now = this.$root.$data.unixTime
    if (!now || !this.alert.timestamp) return null
    return this.$moment.duration(now - this.alert.timestamp, 'seconds') 
  }

}

