






























import { Component, Vue, Prop  } from 'vue-property-decorator'
import AlertListItem from './AlertListItem.vue'
import { Room } from '@/types'
import Alert from '@/model/Alert'

@Component({
  components: {
    AlertListItem
  }
})

export default class HumidityControl extends Vue {

  @Prop() readonly room!: Room
  @Prop() readonly title?: string
  @Prop() readonly dense?: boolean

  get alerts() {
    let alerts = this.room 
      ? this.$root.$data.alerts.filter((a: Alert) => a.roomId == this.room.id)
      : this.$root.$data.alerts

    return alerts.filter((a: Alert) => a.isActive())
  }

}


