















































































































import { Component, Vue, Inject } from 'vue-property-decorator'
import { Device, HomieConfig } from '../../types'

type ResolvePromise = (config?: HomieConfig) => void;
type RejectPromise = () => void;

@Component({})
export default class ConfigDialog extends Vue 
{
    @Inject() prompt!: Function

    // data
    dialog = false
    resolve: ResolvePromise|null = null
    reject: RejectPromise|null = null
    device: Device|null = null
    config: HomieConfig = {}
    options = {
        color: 'primary',
        zIndex: 200,
        fullScreen: true
    }
    relayCount = 0

    get show() {
        return this.dialog
    }
    
    set show(value) {
        this.dialog = value
        if (value === false) {
            this.cancel()
        }
    }

    get isEbbflow() {
        return this.device?.fw?.name?.match(/ebbflow/)
    }

    get isOnline() {
        return this.device?.state == 'ready'
    }
    

    open(device: Device, options = {}): Promise<HomieConfig>
    {
        
        this.dialog = true
        this.options = Object.assign(this.options, options)
        this.device = device
        this.config = JSON.parse(JSON.stringify(device.implementation?.config))

        if (!this.config)
            this.config = {}

        if (!this.config.settings)
            this.config.settings = {}
        // console.log('devconfig', this.config)

        let fw_name = device.fw?.name
        if (fw_name)
        {
            // ipcc defaults
            let settings = this.config.settings
    
            if (device.fw?.name?.match(/ipcc/) && !settings?.ntp_server) 
                settings.ntp_server = "br.pool.ntp.org"
    
            // relays
            if (fw_name.match(/sonoff-(basic|1ch)/) ) this.relayCount = 1
            else if (fw_name.match(/sonoff-?(dual|2ch)/) ) this.relayCount = 2
            else if (fw_name.match(/sonoff-4ch/) ) this.relayCount = 4
            else this.relayCount = 0
        }



        return new Promise<HomieConfig>((resolve: ResolvePromise, reject: RejectPromise) => {
            this.resolve = resolve
            this.reject = reject
        })
    }

    ok() {
        if (!this.isOnline) 
            return this.cancel()

        if (this.device?.state == "ready") 
        {
            if (this.resolve != null)
                this.resolve(this.config)
            this.dialog = false
        }
        else 
        {
            this.$toast.error("O dispositivo está desconectado.")
        }
    }

    cancel() {
        // this.resolve(false)
        this.dialog = false
    }

    configRelay(index: number) {
        let settings = this.config.settings
        if (settings == null) return
        let key = `relay${index}_role`
        this.prompt(`Função relay #${index}`, settings[key], {light:true}).then( (v: string) => {
            if (settings)
                settings[key] = v
        })
    }

}

